

/*
 *  Admin Footer
 *
 */
 
 #wpfooter{

    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-bottom: 15px;
  
    padding-left: 34px;
    padding-right: 34px;
  
    max-width: 1140px;

    @media ( max-width: 782px){
      margin-left: 0 !important;
    }
  
    .clear{ display: none; }

    a:link,a:visited,a:hover,a:focus,a:active{ color: var( --yetix--color--orange ); }
  
  }
  
  #footer-left    { padding-right: 20px; }
  #footer-upgrade { }
  
  .yetix_request__admin-footer-upgrade{
    display: inline-block;
    text-align: right;
  
    img{
      display: block;
      margin-bottom: 8px;
    }
  
    span{
      display: block;
    }
  
  }