

// Fab Four technique: https://medium.freecodecamp.com/the-fab-four-technique-to-create-responsive-emails-without-media-queries-baf11fdfa848#.65xejb13e
// (Credit to Rémi Parmentier)

// autre source :
// https://www.sitepoint.com/responsive-css-patterns-without-media-queries/
// https://gist.github.com/elliotdavies/df04abf0ac1b0ef4bcff0fe62455812b

@mixin yetix_fab_four( $mobile, $desktop, $bp ){
    
    min-width: $desktop ;
    width: calc( ( #{$bp} - 100%) * 1000 );
    max-width: $mobile;

}



@mixin yetix_responsive_table(){

    thead {
        border: none;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
      }

      tr {

        display: block;
        background-color: var( --yetix--color--white );
        margin-bottom: 2em;
        border : none !important;
        border-bottom: 2px solid rgba(0,0,0,0.2) !important;
      }

      td {
        display: block;
        border : none !important;
        text-align: right !important;
        padding: .78571429em .78571429em;
        &::before {
          /*
          * aria-label has no advantage, it won't be read inside a table
          content: attr(aria-label);
          */
          content: attr(data-label);
          float: left;
          font-weight: 600;
        }


      }
}