
@import '_core/_config-admin.scss';
@import '_core/_bootstrap.scss';

.yetix{
  @include yetix_wp_core_ui() ;
}

body.wp-admin{
  background-color: var( --yetix--admin--page--background-color );
}

#wpbody{

  .yetix_admin--has_header & {
    padding-top: calc( $admin_header_height + 20px );
    @media( max-width:600px ){ padding-top: calc( $admin_header_height + 46px) ; }
    transform: translateY(16px);
  }


  .notice{
    margin-right: 34px;
    margin-left: 14px;
    max-width: 1140px;
    box-sizing: border-box;

    @media(max-width:782px){ margin-left: 24px; }
  }

}

#wpbody-content{

  box-sizing: border-box;
  * { box-sizing: border-box; }

  padding-left: 34px;
  padding-right: 34px;

  width: 100%;
  width: calc( 100% + 20px );
  /* min-width: calc( 100% + 20px ); */

  margin-left: -20px;
  padding-top: 20px;

  max-width: 1140px;
  max-width: 1210px;
  @media ( max-width:782px ){  padding-left : 44px; }

  > .notice{
    margin-right: 0;
    margin-left: 0;
  }



  @include yetix_semantic_ui_consistency();
  @include yetix_admin_semantic_ui_override();
  
  @import '_admin/_header' ;
  
  .yetix{
    @import '_admin/_copy' ;
    @import '_admin/_general-settings-form' ;
    @import '_admin/_general_settings_users_form' ;
    @import '_admin/_users_table' ;
    
  }


  /*
  by #id
  */
  #yetix_development{

    &__general_settings_form {
      .form-table td{ padding-right: 0; }
    }

    &__settings_form{
      
    /* loading */
    &.ui.form.loading::before {
      background: var( --yetix--admin--page--background-color );
      background-color: rgba( var( --yetix--admin--page--background-color--rgb ), 0.7);
    }

  }

    &__ticket_fields_settings{
      padding-bottom: 70px;
    }

    &__settings_zendesk_domain{
      width: 100%;
      max-width: calc(100% - 200px);
    }
  
  }

  @import '_admin/_status' ;


  /*
  by .class
  */
  .yetix_development{

    &__ticket_fields_settings_form{}

    &__settings_zendesk_domain-wrapper{
      width: 100%;
    }

  }
  
  @import '_admin/_settings_field_board' ;
  
}
 
@import '_admin/_footer' ;