/*
Fields Board
*/
.yetix_request__settings_field_board{

    .title{
        display: flex;
        align-items: center;
        @media (max-width:767px){ flex-wrap: wrap;}
    }

    .content > td {
        padding: 0 0.78571429em 0.78571429em 0.78571429em;
        padding: 0 0.78571429em calc( 2 * 0.78571429em ) 0.78571429em;
    }

    .content .field {
        margin-bottom: 0;
    }
    .content .field + .field{
        margin-top: 16px;
    }

    .field-name-clicker{
        position: relative;
        line-height: 2em;
        border-radius: 4px;

        /* flex-grow: 1; */
        width: 100%;
        
        transition: all 300ms;

        &:hover,&:focus,&:active,&.active{
            color: var( --yetix--color--orange );
            /*
            background-color: rgba(255,255,255,0.5);
            box-shadow: inset 0 0 4px white;
            */
        }

        .lock.icon{
        position: absolute;
        right: 8px;
        margin: auto;
        bottom: 0; top: 0;
        }
    }

    [class*=th-],
    [class*=td-field-]{
        padding: .78571429em .78571429em !important;
        align-items: center;
    }

    .td-field-name{
        display: flex;
        justify-content: space-between;
        margin-bottom: 0;
        padding-right: 0 !important;
        padding-left: 0 !important;
    }

    .th-name{
        padding-left: 3.05em !important;
    }

    /* table widths */

    thead{
        @media (max-width:767px){ display: none !important; }
        > tr { display: flex; }
    }

    tbody{
        position: relative;
    }

    .the-field{
        width: 100%;
        visibility: visible !important;
        > td{ display: block; }

        label{ font-weight: 500; }
        &.ready-for-drag .content{ display: none;  }

    }

    .accordion > tr{ }
    .accordion > tr > td { padding: 0; }
    .accordion > tr > td > .title { padding: 0; margin: 0; }
    .accordion > tr > td > .content { padding: .78571429em .78571429em !important; padding-top: 0; }
    

    .accordion th{ display: block; }
    

    .th-name,   .td-field-name   { flex-grow: 1; }
    .th-type,   .td-field-type   { width: 150px; }
    .th-format, .td-field-format { width: 170px; }

    .th-display,   .td-field-display { width: 110px; }
    .th-required, .td-field-required { width: 110px; }

    .th-required{}
    .th-display{}

    .field-name-type{
        min-width: 120px;
    }

    .td-field-name{
        @media (max-width:767px){ width: 100% !important;}

        > .icon.handle{
            opacity: 0.5;
            cursor: move;
            height: 2.1em;

            margin-right: 0;
            padding: 0 0.5em;
            width: auto;

            &:before{
            display: inline;
            line-height: 2em;
            }
        }
    }

    /* sortable fields */

    .the-field.ui-sortable-helper{
        background-color: var( --yetix--admin--field-board--sortable-helper--background-color );
        td{ opacity: 0.3;}
    }

    .the-field.ui-sortable-placeholder{
        visibility: visible !important;
        td{ background-color: var( --yetix--admin--field-board--sortable-placeholder--background-color ); }
    }
    
}