
.yetix_request__admin_header{

    position: absolute;
    margin: auto;
    width: auto;
    height: calc( $admin_header_height + 16px );
    top: -16px;
    left: -20px;
    right: 0px;
    @media( max-width : 600px ){
        top : 30px;
        left: -10px;
    }
    
    display: flex;
    align-items:stretch;
    justify-content: space-between;
    padding: 0 15px;

    background-color: var( --yetix--admin--header--background-color );
    
    border-bottom: 10px solid var( --yetix--color--duck-blue );

    &__brand{
        display: flex;
        align-items:flex-end;

        padding-right: 15px;
    }

    &__support{
        align-self : center ;
        text-align: right;
        
        padding-left: 15px;

        a{
            font-size: var( --yetix--text--font--size );
            color: var(--yetix--color--duck-blue);

            &:focus,&:hover,&:active{
                text-decoration: none;
            }
        }
    }

    &__logo_canvas{
        width: 194px;
        height: 118px;
        overflow: hidden;

        img{ display: block; width: 100%; height: auto; }
    }
}